(() => {
  const refs = {
    openModalBtn: document.querySelector('[data-modal-open-main]'),
    closeModalBtn: document.querySelector('[data-modal-close-main]'),
    modal: document.querySelector('[data-modal-main]'),
  };

  refs.openModalBtn.addEventListener('click', toggleModal);
  refs.closeModalBtn.addEventListener('click', toggleModal);

  function toggleModal() {
    document.body.classList.toggle('modal-open-main');
    refs.modal.classList.toggle('is-hidden');
  }
})();